<template>
  <div id="ReportContainer" :class="{ 'edit-mode': isInEditMode }">
    <ul class="dashbord-widget" style="float: none !important; margin-top: 10px !important;">
      <template v-for="(item, index) in numberLayout">
        <NumberChart v-if="item.chartType && item.chartType.toLowerCase() === 'numbers_chart'"
          :isSystemReport="item.isSystemReport" :fromDate="item.fromDate" :toDate="item.toDate"
          :filterData="item.filterData" :index="item.i" :icon="item.icon" :reportName="item.reportName"
          :chartType="item.chartType" :reportId="item.reportId" :dashboardid="dashboardid" :moduleCode="moduleCode"
          :isInEditMode="isInEditMode" :numberLayout="numberLayout" />
      </template>
    </ul>
    <template v-if="toDoList">
      <div class="grid-toDo-list">
        <div>One</div>
        <div>Two</div>
        
        <grid-layout :layout.sync="layout" :col-num="12" :row-height="30" :is-draggable="draggable"
          :is-resizable="resizable" :vertical-compact="true" :use-css-transforms="true">
          <grid-item v-for="(item, index) in layout" :static="item.static" :x="item.x" :y="item.y" :w="item.w" :h="item.h"
            :i="item.i" @resized="resizedEvent" @resize="resizeEvent" :class="item.reportId" :key="index">
            <div :class="{
              'graph-widget align-self-stretch ticketingDashboardHead reportcontainer mb-2': true,
            }">

              <div class="dashboard-boxes graph-widget-box bg-white">
                <div class="graph-widget-heading float-left w-100">
                  <span class="text-truncate float-left w-75">{{ item.reportName }}</span>
                  <div class="mw-80 float-right">
                    <a href="javascript:;" :class="{
                      'hlkDeleteReport float-right': true,
                      'd-none': !isInEditMode,
                    }" title="Remove" @click="removeItem(item.i, item.reportId, item.chartType)">
                      <span class="icon_close">
                        <i class="fa fa-times"></i>
                      </span>
                    </a>
                    <!-- <a href="javascript:;" class="zoomin rptexpand ancdashboard float-right" @click="ExpandCollapseReport($event,item)" >
                              <span class="icon_expand">
                                  <i class="fa fa-expand"></i>
                              </span>
                          </a> -->
                    <a href="javascript:;" @click="toggleWidgetDropdown(item)" class="float-right">
                      <span class="icon_expand"><i class="fa fa-cog"></i></span>
                    </a>
                    <div v-if="item.showDropdown" class="widget-dropdown">
                      <!-- Add your dropdown menu items here -->
                      <div @click="openPopup(item.reportId, item.reportName, 'Date Filter')">
                        Date Filter
                      </div>
                      <div @click="openPopup(item.reportId, item.reportName, 'Data Filter')">
                        Data Filter
                      </div>
                      <div @click="openPopup(item.reportId, item.reportName, 'Graph Filter')">
                        Graph Filter
                      </div>
                    </div>
                    <!-- Include the PopupComponent -->
                  </div>
                </div>
                <div :class="{
                  'graph-widget-content bg-white float-left w-100': true,
                  'd-flex  align-content-center justify-content-center': true,
                }">
                  <!-- <Skeletor  v-if="layout"/>   -->
                  <LineChart v-if="item.chartType && item.chartType.toLowerCase() === 'line'"
                    :ref="'line' + item.reportId + '_' + item.widgetId" :fromDate="item.fromDate" :toDate="item.toDate"
                    :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                    :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height">
                  </LineChart>
                  <bar-chart
                    v-if="item.chartType && (item.chartType.toLowerCase() === 'bar' || item.chartType.toLowerCase() === 'horizontal_bar')"
                    :ref="('bar' ? item.chartType.toLowerCase() === 'bar' : 'horizontal_bar') + item.reportId + '_' + item.widgetId"
                    :fromDate="item.fromDate" :toDate="item.toDate" :filterData="item.filterData" :moduleCode="moduleCode"
                    :index="item.i" :reportName="item.reportName" :chartType="item.chartType" :reportId="item.reportId"
                    :width="item.width" :height="item.height"></bar-chart>
                  <PieChart v-if="item.chartType && item.chartType.toLowerCase() === 'pie'"
                    :ref="'pie' + item.reportId + '_' + item.widgetId" :fromDate="item.fromDate" :toDate="item.toDate"
                    :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                    :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height">
                  </PieChart>
                  <DonutChart v-if="item.chartType && item.chartType.toLowerCase() === 'donut'"
                    :ref="'donut' + item.reportId + '_' + item.widgetId" :fromDate="item.fromDate" :toDate="item.toDate"
                    :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                    :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height">
                  </DonutChart>
                  <TableChart v-if="item.chartType == null || item.chartType.toLowerCase() === 'table'"
                    :fromDate="fromDate" :ref="'table' + item.reportId + '_' + item.widgetId" :toDate="toDate"
                    :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                    :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height">
                  </TableChart>
                </div>
              </div>
            </div>
          </grid-item>
        </grid-layout>
      </div>
    </template>
    <template v-else>
     
      <grid-layout :layout.sync="layout" :col-num="12" :row-height="40" :is-draggable="draggable"
        :is-resizable="resizable" :vertical-compact="true" :use-css-transforms="true">
        <grid-item v-for="(item, index) in layout" :static="item.static" :x="item.x" :y="item.y" :w="item.w" :h="item.h"
          :i="item.i" @resized="resizedEvent" @resize="resizeEvent" :class="item.reportId" :key="item.i">

          <div :class="{
            'graph-widget align-self-stretch ticketingDashboardHead reportcontainer mb-2': true,
          }">
            <div class="dashboard-boxes graph-widget-box bg-white">
              <div class="graph-widget-heading float-left w-100">
                <span class="text-truncate float-left w-75" :title="$t(item.reportName)">{{ $t(item.reportName) }}</span>
                <div class="mw-80 float-right">

                  <a href="javascript:;" :class="{
                    'hlkDeleteReport float-right': true,
                    'd-none': !isInEditMode,
                  }" title="Remove" @click="removeItem(item.i, item.reportId, item.chartType)">
                    <span class="icon_close">
                      <i class="fa fa-times"></i>
                    </span>
                  </a>

                  <a v-if="item.actualWidgetType != 'Table' && item.actualWidgetType != null" href="javascript:;"
                    @click="toggleWidgetDropdown(item)" class="float-right">
                    <span class="icon_expand"><i class="fa fa-cog"></i></span>
                  </a>
                  <div v-if="item.showDropdown" class="widget-dropdown">
                    <!-- Add your dropdown menu items here -->
                    <div v-if="item.isSystemReport" :class="{ 'd-none': isInEditMode }"
                      @click="openPopup(item, 'Date Filter')">
                      Date Filter
                    </div>
                    <!-- <div @click="openPopup(item.reportId, item.reportName, 'Data Filter')">
                      Data Filter
                    </div> -->
                    <div @click="openPopup(item, 'Graph Filter')">
                      Graph Filter
                    </div>
                  </div>
                </div>
              </div>
              <div :class="{
                'graph-widget-content bg-white float-left w-100': true,
                'd-flex  align-content-center justify-content-center': true,
              }">

                <!-- <Skeletor  v-if="layout"/>   -->
                <LineChart v-if="item.chartType && item.chartType.toLowerCase() === 'line'
                  " :isSystemReport="item.isSystemReport" :dashboardid="dashboardid"
                  :ref="'line' + item.reportId + '_' + item.widgetId" :fromDate="item.fromDate" :toDate="item.toDate"
                  :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                  :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height"
                  @set-widget-data="SetWidgetData"></LineChart>
                <bar-chart v-if="item.chartType &&
                  (item.chartType.toLowerCase() === 'bar' ||
                    item.chartType.toLowerCase() === 'horizontal_bar')
                  " :isSystemReport="item.isSystemReport" :dashboardid="dashboardid"
                  :ref="('bar' ? item.chartType.toLowerCase() === 'bar' : 'horizontal_bar') + item.reportId + '_' + item.widgetId"
                  :fromDate="item.fromDate" :toDate="item.toDate" :filterData="item.filterData" :moduleCode="moduleCode"
                  :index="item.i" :reportName="item.reportName" :chartType="item.chartType" :reportId="item.reportId"
                  :width="item.width" :height="item.height" :resizeheight="resizeheight"
                  @set-widget-data="SetWidgetData"></bar-chart>
                <PieChart v-if="item.chartType && item.chartType.toLowerCase() === 'pie'
                  " :isSystemReport="item.isSystemReport" :dashboardid="dashboardid"
                  :ref="'pie' + item.reportId + '_' + item.widgetId" :fromDate="item.fromDate" :toDate="item.toDate"
                  :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                  :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height"
                  :resizeheight="resizeheight" @set-widget-data="SetWidgetData">
                </PieChart>
                <DonutChart v-if="item.chartType && item.chartType.toLowerCase() === 'donut'
                  " :isSystemReport="item.isSystemReport" :dashboardid="dashboardid"
                  :ref="'donut' + item.reportId + '_' + item.widgetId" :fromDate="item.fromDate" :toDate="item.toDate"
                  :filterData="item.filterData" :moduleCode="moduleCode" :index="item.i" :reportName="item.reportName"
                  :chartType="item.chartType" :reportId="item.reportId" :width="item.width" :height="item.height"
                  :resizeheight="resizeheight" @set-widget-data="SetWidgetData">
                </DonutChart>
                <TableChart v-if="item.chartType === null ||
                  item.chartType.toLowerCase() === 'table'
                  " :tableheight="tableheight" :isSystemReport="item.isSystemReport" :dashboardid="dashboardid"
                  :fromDate="item.fromDate" :toDate="item.toDate" :filterData="item.filterData"
                  :ref="'table' + item.reportId + '_' + item.widgetId" :moduleCode="moduleCode" :index="item.i"
                  :reportName="item.reportName" :chartType="item.chartType === null ? 'Table' : item.chartType"
                  :reportId="item.reportId" :width="item.width" :height="item.height"></TableChart>
              </div>
              <div v-if="item.isSystemReport == false" class="view-report" @click="ViewReport(item.reportId)">View Report
              </div>
            </div>
          </div>
        </grid-item>
      </grid-layout>
    </template>
    <popup-component ref="popup" v-if="showPopup" :moduleCode="moduleCode" :dashboardid="dashboardid"
      :content-type="popupContentType" :report-id="popupReportId" :report-name="popupReportName" :popup-item="popupItem"
      @close-popup="closePopup" />
  </div>
</template>

<script>
import BarChart from "../Dashboard/Widget/BarChart.vue";
import LineChart from "../Dashboard/Widget/LineChart.vue";
import PieChart from "../Dashboard/Widget/PieChart.vue";
import DonutChart from "../Dashboard/Widget/DonutChart.vue";
import TableChart from "./Widget/TableChart.vue";
import NumberChart from "../Dashboard/Widget/NumberChart.vue";
import PopupComponent from "./PopupComponent.vue";
import DataService from "../../services/DataService";
export default {
  components: {
    BarChart,
    LineChart,
    PieChart,
    DonutChart,
    TableChart,
    NumberChart,
    PopupComponent,
  },
  props: {
    reports: null,
    selectedDashboardName: null,
    // fromDate: null,
    // toDate: null,
    moduleCode: null,
    dashboardid: Number,
    isdraggable: Boolean,
    isresizable: Boolean,
  },
  data: function () {
    return {
      layout: [],
      numberLayout: [],
      layoutNew: [],
      isLoading: false,
      toDoList: false,
      draggable: this.isdraggable,
      resizable: this.isresizable,
      index: 0,
      removedWidgets: [],
      isInEditMode: false,
      showPopup: false,
      popupContentType: "", // This will determine the content to display
      popupItem: null,
      chartsArray: [],
      fromDate: "",
      toDate: "",
      chartType: "",
      resizeheight: null,
      tableheight: null,
      widgetData: null,
    };
  },
  mounted() {
    this.index = this.layout.length;
  },
  created() {
    this.CreateReportWidget();
    this.popupItem = this.layout.find((item) => item.reportId);

  },
  methods: {
    SetWidgetData(data, type) {
      // console.log(type);
      this.widgetData = data;
    },
    ViewReport(reportId) {
      const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
      const route = '/Report/viewcustomreport/' + encodedReportID;
      // Open the route in a new tab
      window.open(route, '_blank');
    },
    capitalizeChartType(chartType) {
      if (chartType) {
        // Capitalize the first letter
        return chartType.charAt(0).toUpperCase() + chartType.slice(1);
      } else {
        return "";
      }
    },
    openPopup(item, contentType) {
      this.popupItem = this.layout.find((x) => x.reportId === item.reportId);
      console.log(this.popupItem);
      this.popupContentType = contentType;
      this.popupReportId = item.reportId;
      this.popupReportName = item.reportName;
      this.showPopup = true;
      if (this.showPopup) {
        item.showDropdown = false
      }
    },
    closePopup(dateSpan) {
      this.showPopup = false;
    },
    addStyleInInnerGried() {
      this.$nextTick(() => {
        const Height = document.querySelector(".InnerGried").style.height;
        const width = document.querySelector(".InnerGried").style.width;
        return {
          width: width,
          Height: Height,
        };
      });
    },
    updateChildVariables() {
      
      var vm = this;
      vm.isInEditMode = !vm.isInEditMode;
      vm.draggable = !vm.draggable;
      vm.resizable = !vm.resizable;
    },
    itemTitle(item) {
      let result = item.i;
      if (item.static) {
        result += " - Static";
      }
      return result;
    },
    ExpandCollapseReport: function (event, item) {
      $(event.target).closest(".vue-grid-item").width(996).height(390);
    },
    onResizeStop: function (item) {
      switch (item.chartType) {
        case "Bar":
          this.$refs[
            "bar" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
        case "Horizontal_Bar":
          this.$refs[
            "horizontal_bar" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
        case "Line":
          this.$refs[
            "line" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
        case "Pie":
          this.$refs[
            "pie" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
        case "Donut":
          this.$refs[
            "donut" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
        case "Numbers_Chart":
          this.$refs[
            "numbers_chart" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
        default:
          this.$refs[
            "table" + item.reportId + "_" + item.widgetId
          ][0].FetchData();
          break;
      }
    },
    CreateReportWidget() {
      
      var vm = this;
      vm.layout = [];
      vm.numberLayout = [];
      for (let index = 0; index < vm.reports.length; index++) {
        let width =
          vm.reports[index].widgetWidth != null
            ? parseInt(vm.reports[index].widgetWidth)
            : 3
        let height =
          vm.reports[index].widgetHeight != null
            ? parseInt(vm.reports[index].widgetHeight)
            : 10;
        let screenBlockWidth =
          document.getElementById("main-content").offsetWidth / 12;
        var heightResize = 0;
        if (height >= 4) {
          heightResize = 25 * height;
        } else if (height > 4 && height <= 15) {
          heightResize = 30 * height;
        } else if (height > 15) {
          heightResize = 100 * height;
        }
        if (vm.reports[index].xaxis == null && vm.dashboardid == 0) {
          // Increment x-axis by 3 from 0 to 12
          var x = (index % 4) * 3;
        } else {
          x = vm.reports[index].xaxis != null ? parseInt(vm.reports[index].xaxis) : 0;
        }
        if (vm.reports[index].chartType !== "numbers_chart") {
          vm.layout.push({
            reportId: vm.reports[index].reportId,
            widgetId: vm.reports[index].widgetId,
            reportName: vm.reports[index].reportName,
            chartType: vm.reports[index].chartType,
            actualWidgetType: vm.reports[index].actualWidgetType,
            isSystemReport: vm.reports[index].isSystemReport,
            fromDate: vm.reports[index].fromDate,
            toDate: vm.reports[index].toDate,
            filterData: vm.reports[index].filterData,
            x: x,
            y: vm.reports[index].yaxis != null ? parseInt(vm.reports[index].yaxis) : 0,
            w: width,
            h: height,
            i: index.toString(),
            width: screenBlockWidth * width,
            height: heightResize, //(35 * height),
            static: false,
          });
        } else {
          vm.numberLayout.push({
            reportId: vm.reports[index].reportId,
            widgetId: vm.reports[index].widgetId,
            reportName: vm.reports[index].reportName,
            chartType: vm.reports[index].chartType,
            actualWidgetType: vm.reports[index].actualWidgetType,
            isSystemReport: vm.reports[index].isSystemReport,
            icon: vm.reports[index].icon,
            fromDate: vm.reports[index].fromDate,
            toDate: vm.reports[index].toDate,
            filterData: vm.reports[index].filterData,
            // x: x,
            // y: vm.reports[index].yaxis != null ? parseInt(vm.reports[index].yaxis) : 0,
            // w: width,
            // h: height,
            i: index.toString(),
            // width: screenBlockWidth * width,
            // height: heightResize, //(35 * height),
            static: false,
          });
        }
      }
    },
    SaveEditedChanges() {
      
      var vm = this;
      var deletedArr = vm.removedWidgets.join(", ");
      var data = {
        reports: vm.layout,
        DeletedRec: deletedArr,
        dashboardId: vm.dashboardid,
        dashboardName: vm.selectedDashboardName,
      };
      // console.log(data);
      var params = JSON.stringify(data);
      vm.confirmR(
        vm.$t("ConfirmDynamicReportsEdit"),
        true,
        vm.$t("Confirm"),
        function (result) {
          DataService.SaveEditsInDashboardReports(params).then((response) => {
            try {
              vm.ShowAlert(
                vm.$t("EditedSuccess", [vm.$t("Changes")]),
                "success",
                true,
                vm.$t("Alert")
              );
              vm.$parent.GetDashboard(vm.dashboardid);
            } catch (exinner) {
              return HandleErrorOccured(exinner, "failure");
            }
          });
        },
        function () {
          
          vm.$parent.GetDashboard(vm.dashboardid);
        },
      );
    },
    increaseWidth: function () {
      let width = document.getElementById("content").offsetWidth;
      width += 20;
      document.getElementById("content").style.width = width + "px";
    },
    decreaseWidth: function () {
      let width = document.getElementById("content").offsetWidth;
      width -= 20;
      document.getElementById("content").style.width = width + "px";
    },
    removeItem: function (i, reportId, type) {
      var vm = this;
      if (vm.layout.length <= 2 && type !== 'numbers_chart') {
        vm.ShowAlert(vm.$t('ConfirmModalNotDelete'), "warning");
      } else {
        vm.confirmR(
          vm.$t("ConfirmModalDelete"),
          true,
          vm.$t("Confirm"),
          function () {
            if (type === 'numbers_chart') {
              const indexToRemove = vm.numberLayout.findIndex(
                (item) => item.i === i && item.reportId === reportId && item.chartType === type
              );
              if (indexToRemove !== -1) {
                vm.numberLayout.splice(indexToRemove, 1);
              }
            } else {
              const index = vm.layout.map((item) => item.i).indexOf(i);
              vm.layout.splice(index, 1);
            }
            vm.removedWidgets.push(reportId);
          }
        )
      }
    },
    toggleWidgetDropdown(item) {
      
      this.layout.forEach(x=>{
        if(item.reportId == x.reportId)
        {
        this.$set(x, "showDropdown", !item.showDropdown);
        }
        else
        {
          this.$set(x, "showDropdown", false);
        }
      });
    },
    editWidget(item) {
      // Add logic for editing the widget
      console.log("Editing widget:", item);
    },

    configureWidget(item) {
      // Add logic for configuring the widget
      console.log("Configuring widget:", item);
    },
    openWidgetSetting(item) {
      this.selectedLayout = item;
      this.showWidgetsetting = !this.showWidgetsetting;
    },
    addItem: function () {
      const item = {
        x: 0,
        y: 0,
        w: 2,
        h: 2,
        i: this.index + "",
        whatever: "bbb",
      };
      this.index++;
      this.layout.push(item);
    },
    resizeEvent: function (i, newH, newW, newHPx, newWPx) {
      this.resizeheight = newH;
      this.tableheight = newH;
      console.log(
        "RESIZE i=" +
        i +
        ", H=" +
        newH +
        ", W=" +
        newW +
        ", H(px)=" +
        newHPx +
        ", W(px)=" +
        newWPx
      );
    },
    resizedEvent: function (i, newH, newW, newHPx, newWPx) {
      console.log(
        "RESIZE i=" +
        i +
        ", H=" +
        newH +
        ", W=" +
        newW +
        ", H(px)=" +
        newHPx +
        ", W(px)=" +
        newWPx
      );
      let screenBlockWidth =
        document.getElementById("main-content").offsetWidth / 12;
      //const screenBlockHeight = window.innerHeight;
      this.layout[i].width = screenBlockWidth * newW;
      if (newH >= 4) {
        this.layout[i].height = 25 * newH;
      } else if (newH > 4 && newH <= 15) {
        this.layout[i].height = 30 * newH;
      } else if (newH > 15) {
        this.layout[i].height = 100 * newH;
      }
    },
  },
};
</script>
<style scoped>
.view-report {
  margin-left: 10px;
  margin-top: 10px;
  text-decoration: underline;
  color: #1f77b4;
  font-weight: 400;
  cursor: pointer;
  float: left;
}

.grid-toDo-list {
  display: grid;
  grid-template-columns: 0.7fr 1fr;
}

.grid-toDo-list>div:nth-child(1) {
  grid-column: span 2;
  /* Make the first div span 2 columns (full width) */
}

.widget-dropdown {
  position: absolute;
  width: 150px;
  top: 40px;
  border-radius: 4px;
  right: 0;
  background: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border: 1px solid #ccc;
  display: flex;
  flex-direction: column;
  z-index: 2;
  /* Ensure the dropdown is above other elements */
}

.widget-dropdown div {
  padding: 0 8px;
  border: none;
  background: none;
  cursor: pointer;
  text-align: left;
}

.vue-grid-layout {
  background: #eee;
}

.vue-grid-item:not(.vue-grid-placeholder) {
  background: #ccc;
  border: 1px solid black;
}

.vue-grid-item .resizing {
  opacity: 0.9;
}

.vue-grid-item .static {
  background: #cce;
}

.vue-grid-item .text {
  font-size: 24px;
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 100%;
  width: 100%;
}

