<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Linechrt text-center">
    <loader :isVisible="isLoading"></loader>
    <template v-if="LineWidgetData.length === 0 && isLoading === false">
      <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }"
        style="display: flex; align-items: center; justify-content: center">
        {{ $t("NoRecordfound") }}
      </div>
    </template>
    <template v-else>
      <div :id="'lineDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index">
        <div
          :class="{ 'line d-inline-block mt-2 reportBlock t-min-height align-center c3 w-100': LineWidgetData.length > 0, 'line d-inline-block mt-2 align-center c3 w-100': LineWidgetData.length == 0 }"
          :controller="moduleCode" style="display: flex!important;"
          :id="'div_LineChart_' + action.replace(/\s+/g, '_') + index"
          :style="{ 'width:60%; float:left; text-align:center;': LineWidgetData.length > 0, 'width:100%;text-align:center;': LineWidgetData.length == 0 }">
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";

export default {
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    dashboardid: Number,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    index: null,
    width: null,
    height: null,
    reportId: null,
    isSystemReport: null,
    resizeheight: null,
    filterData: "",
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "line",
      xaxis: "",
      yaxis: "",
      LineWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      ChartName: null,
    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);
    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData();
    },
    filterData: function () {
      this.FetchData();
    },
  },
  created() {
    this.FetchData();
    this.action = this.action.replace(/\s/g, "");
  },
  methods: {
    async FetchData() {
      
      var vueObj = this;
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      await DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if (response.data.result != null) {
              vueObj.LineWidgetData = response.data.result.data;
              vueObj.xaxis = response.data.result.Xaxis;
              vueObj.yaxis = response.data.result.Yaxis;
              vueObj.bindWidget(vueObj);
            }
          }
        }
        vueObj.isLoading = false;
      });
    },
    bindWidget(vueObj) {
      
      if (vueObj.charT_TYPE && vueObj.LineWidgetData.length > 0) {
        // var chartType = this.charT_TYPE;
        var xCategory = vueObj.xaxis;
        var yCategory = vueObj.yaxis;
        var arrAU = [];
        var categoryArray = [];
        $.each(vueObj.LineWidgetData, function (index, element) {
          //arrAU.push([element.NAME, element.VALUE]);
          categoryArray.push(element.NAME);
          arrAU.push(element.VALUE);
        });
        arrAU.splice(0, 0, yCategory);
        // var uniqueWidgetTypes = [
        //   ...new Set(vueObj.LineWidgetData.map((item) => item.WIDGET_TYPE)),
        // ];
        // var xValues = uniqueWidgetTypes;
        // var yValues = [vueObj.reportName];
        // uniqueWidgetTypes.forEach((widgetType) => {
        //   var valuesForWidgetType = vueObj.LineWidgetData.filter(
        //     (item) => item.WIDGET_TYPE === widgetType
        //   ).map((item) => item.VALUE);
        //   // yValues.push(widgetType);
        //   yValues = yValues.concat(valuesForWidgetType);
        // });
        // var columns = [xValues, yValues];
        // console.log(columns);
        var selector = "#div_LineChart_" + vueObj.action.replace(/\s+/g, '_') + vueObj.index;
        // eslint-disable-next-line no-undef
        $(selector).html("");
        // eslint-disable-next-line no-undef
        let LineChart = {
          bindto: selector,
          data: {
            columns: [arrAU],
            type: 'line',
            empty: {
              label: {
                position: "right",
                text: vueObj.$t("NoRecordfound"),
              },
            },

          },
          tooltip: {
            grouped: false
          },
          size: {
            height: this.resizeheight ? this.resizeheight * 40 : this.height + 100,
          },
          legend: {
            show: true,
            position: "bottom",
          },
          axis: {
            x: {
              label: {
                text: xCategory,
                position: "outer-center",
              },
              categories: categoryArray,
              type: "category",
            },
            y: {
              label: {
                text: yCategory,
                position: "outer-middle",
              },
              padding: {
                top: 0,
              },
            },
          },
        };

        var Linea = c3.generate(LineChart);

        $(selector).data("c3-chart", Linea);
      }
    },
  },
};
</script>

<style src="c3/c3.css"></style>
<style>
.custom-chart-div.Linechrt .reportBlock {
  transform: initial;
}
</style>
